table,
td,
th {
  border: 1px solid black;
  height: 35px;
}

.transaction_history{
 width: 400px;
  border-collapse: collapse;
  box-shadow: 4px 4px 6px -2px rgba(0, 0, 0, 0.39);
  margin-bottom: 30px;
}

.table_head {
  background-color: rgb(76, 151, 236);
  color: white;
}

.table_item {
  color: grey;
}

.table_item:nth-child(even) {
  background-color: rgb(228, 225, 225);
}